body {
  background-color: #b2aba9;
}

.container {
  background-color: #f2f2f2;
  justify-content: center;
 width: 50%;
 margin-left: 26%;
 margin-top: 5%;
  text-align: center;
  color: white;
}
input[type=text], select{
  width: 80%;
  padding: 12px 20px;
  margin: 8px 0;
  display: inline-block;
  border: 1px solid #ccc;
  border-radius: 4px;
  box-sizing: border-box;
}
button{
width:30%;
background-color: #4caf50;
color:white;
padding: 14px 20px;
margin: 8px 0;
border: none;
border-radius: 4px;
cursor: pointer;

}
button:hover{
  background-color: #45a049;
}
